.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200 px;

  grid-template-areas:
    "h h "
    "n c";
  grid-template-rows: 50px 1fr;
  grid-template-columns: 1fr 20fr;
  /* grid-gap: 8px; */

}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(77, 237, 144);
  font-size: 30px;
 
}


